/* src/App.css */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
}

h1, h2 {
  color: #0056D2;
}

p {
  color: #333;
}



/*header section*/
.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  background-color: #0056D2;
  color: white;
  max-width: 100%;
  position: relative;
  z-index: 10; /* Ensure the header is on top of other content */
}

.logo {
  display: flex;
  align-items: center;
  font-size: 1.4rem;
  font-weight: bold;
}

.logo img {
  margin-right: 5px;
  height: auto;
  width: 80px;
  border-radius: 10px;
}

/* Use an image or icon for the menu icon */
.menu-icon {
  display: none;
  cursor: pointer;
}

.menu-icon img {
  width: 50px;
  height: 70px;
}

/* Navigation styles */
nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin-left: 10px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  padding: 10px 13px;
  transition: all 0.2s ease-in-out;
}

nav ul li a:hover {
  background-color: #e5c799;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}


/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .menu-icon {
    display: block;
  }

  nav ul {
    flex-direction: column;
    position: fixed; /* Position the menu relative to the viewport */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /* Center the menu */
    background-color: rgba(62, 119, 204, 0.952); /* Add a semi-transparent background */
    padding: 20px;
    border-radius: 10px;
    display: none; /* Hide the menu by default */
    z-index: 100; /* Ensure the menu appears above other content */
    width: 50%; /* Optional: limit the width */
  }

  /* Show the menu when open */
  nav.open ul {
    display: flex;
    justify-content: center;
  }

  nav ul li {
    margin-left: 0;
    text-align: center;
  }

  nav ul li a {
    padding: 15px;
    display: block;
    color: white;
    font-size: 1.3rem; /* Increase font size for visibility */
  }
}



/* src/components/HeroSection.css */
.hero-container {
  position: relative; /* Needed to position the pseudo-element */
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  overflow: visible;
  z-index: 1;
}

.hero-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center;
  filter: blur(4px); /* Adjust the blur amount here */
  z-index: 0;
  animation: slideBackground 30s infinite alternate; /* Controls the animation */
}

@keyframes slideBackground {
  0% {
    background-image: url('./assets/image3.png');
  }
  20% {
    background-image: url('./assets/ceo1.jpeg');
  }
  40% {
    background-image: url('./assets/image1.jpg');
  }
  60% {
    background-image: url('./assets/image2.jpg');
  }
  80% {
    background-image: url('./assets/ceo.jpeg');
  }
  100% {
    background-image: url('./assets/image4.jpg');
  }
}

.hero-container > * {
  position: relative; /* Positioning child elements above the blurred background */
  z-index: 1; /* Ensure content appears above the blurred background */
}


.hero-content {
  text-align: center;
  max-width: 80%;
}

.hero-content h1{
  font-size: 2.5rem;
  color: rgb(233, 137, 11);
}

.hero-content p{
  font-size: 1.3rem;
  text-align: center;
  color: black;
  font-weight: bold;
}

.cta-btn {
  background-color: #ebb64e;
  color: #0056D2;
  font-weight: bold;
  padding: 12px 24px;
  border-radius: 5px;
  text-transform: uppercase;
  text-decoration: none;
  transition: background 0.3s ease;
}

.cta-btn:hover {
  background-color: #3e78c9;
  color: white;
}


/*about course*/
.about-course {
  padding: 60px 30px;
  background-color: #F4F4F9;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.about-course h2 {
  font-size: 2rem;
  margin-bottom: 40px;
  color: #333;
}

.course-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: center;
  justify-items: center;
}

.course-list {
  list-style-type: none;
  padding: 0;
  font-size: 1.2em;
  text-align: left;
}

.course-list li {
  margin: 15px 0;
  display: flex;
  align-items: center;
  font-size: 1.1rem;
  color: #333;
  transition: transform 0.3s ease, color 0.3s ease;
}

.course-list li .icon {
  font-size: 1.5rem;
  color: #0056D2;
  margin-right: 15px;
}

.course-list li:hover {
  transform: translateX(10px);
  color: #0056D2;
}

.course-image img {
  width: 550px;
  height: 350px;
  border-radius: 1px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

/* Responsive course content for small screens */
@media (max-width: 768px) {
  .course-content {
    grid-template-columns: 1fr;
  }

  .course-image img {
    max-width: 100%;
  }

  .cta-btn {
    width: 100%;
    padding: 15px;
  }
}




/* src/components/WhyChooseUs.css */
.why-choose-us {
  padding: 50px;
  text-align: center;
  background-color: #d7d7ee; /* Optional background color */
  margin: 10px;
}

.about-us {
  font-size: 1rem;
  line-height: 1.6;
  text-align: justify;
}


.benefits {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  flex-wrap: wrap; /* Responsive layout */
}

.card {
  background-color: #FFBB33;
  padding: 20px;
  border-radius: 10px;
  width: 200px;
  font-size: 1.1em;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow for depth */
  transition: transform 0.3s, background-color 0.3s; /* Smooth transition */
  opacity: 0; /* Start as invisible */
  transform: translateY(20px); /* Start slightly below */
  animation: fadeInUp 0.6s forwards; /* Animation */
}

.card:hover {
  background-color: #0056D2;
  color: white;
  transform: scale(1.05); /* Slightly enlarge on hover */
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive benefits section for small screens */
@media (max-width: 768px) {
  .benefits {
    flex-direction: column; /* Stack cards on smaller screens */
    align-items: center;
  }

  .card {
    margin-bottom: 20px; /* Space between stacked cards */
    width: 90%; /* Ensure cards take up more width on smaller screens */
  }
}

/* Featured Projects Section */
.featured-projects {
  padding: 30px;
  background-color: #F4F4F9;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
}

.projects-carousel {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  gap: 20px; /* Space between cards */
  width: 90%;
  margin-left: 5%;
}

.project-card {
  background-color: #fff; /* White background for each card */
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s; /* Smooth transitions */
  overflow: hidden; /* Ensures the image doesn't overflow */
  box-shadow: 0 10px 20px rgba(98, 107, 98, 0.171); /* Subtle shadow */
}

.project-card:hover {
  transform: scale(1.05); /* Slightly enlarge on hover */
  box-shadow: 0 10px 20px rgba(31, 216, 31, 0.356); /* Enhanced shadow on hover */
}

.project-card img {
  width: 95%;
  padding: 10px;
  border-radius: 10px;
  height: 150px; /* Maintain aspect ratio */
  border-bottom: 3px solid #FFBB33; /* Accent line at the bottom */
}

.projects-carousel div {
  font-weight: bold;
}

/* Testimonials Section */
.testimonials {
  padding: 30px;
  text-align: center;
  background-color: #F4F4F9;
}

.testimonial-slider {
  display: grid; /* Use grid layout */
  grid-template-columns: repeat(2, 1fr); /* Explicitly set to 2 columns */
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap; /* Responsive layout */
  justify-items: center; /* Center items in the grid */
  margin: 10px;
  width: 80%;
  margin-left: 10%;
}

.testimonial-card {
  background-color: #dbbf87;
  border-radius: 10px;
  transition: transform 0.2s;
  max-height: max-content;
}

.testimonial-card:hover {
  transform: scale(1.02);
}

.profile-icon img {
  width: 100px;
  height: auto;
  border-radius: 50%;
  padding: 10px;
}

.project-name {
  font-style: italic;
  margin-bottom: 2px;
}

.message {
  position: relative;
  text-align: justify;
  padding: 10px 50px;
}

.read-more {
  color: #0056D2; /* Link color */
  cursor: pointer;
  font-weight: bold;
}

/* Footer Section */
.footer {
  background-color: #0056D2;
  color: white;
  padding: 20px 20px;
  text-align: center;
  margin: 5px;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 20px;
}

.footer-left,
.footer-center,
.footer-right {
  width: 30%;
  min-width: 200px;
}

.footer-left h4,
.footer-center h4,
.footer-right h4 {
  margin-bottom: 15px;
  font-size: 2rem;
}

.footer-left p,
.footer-right p {
  margin: 8px 0;
  font-size: 1rem;
  color: #d7d7ee;
  text-align: center;
}

.footer-center ul {
  list-style-type: none;
  padding: 0;
}

.footer-center ul li {
  margin: 10px 0;
  font-size: 1rem;
  display: flex;
  align-items: center;
  text-align: center;
  color: #d7d7ee;
  text-decoration: none;
  padding-left: 50px;
}

.footer-center ul li:hover {
  text-decoration: underline;
  color: rgb(223, 180, 145);
}

.footer-center ul li svg {
  margin-right: 15px; /* Adds space between icons and text */
}

.social-icons {
  margin: 15px 0;
}

.social-icons a {
  margin-right: 20px;
  color: rgb(226, 218, 218);
  font-size: 3rem;
  text-decoration: none;
  transition: color 0.3s ease;
}

.social-icons a:hover {
  color: #FFBB33;
}

.footer-bottom {
  margin-top: 20px;
  font-size: 0.9em;
  color: black;
}

/* Reduce the gap between columns */
.footer-container > div {
  margin: 0 5px;
}
